import React, { Component } from 'react';



class TicketButton extends Component {


 handleClick(e) {
    e.preventDefault();
    console.log('The link was clicked.');
  };

  render () {
    return(
      <button onClick={handleClick}>Snag a Ticket</button>
    );
  };

};

export default TicketButton;



